
@mixin headings() {

/*
 * some default link styling
 */

    @function heading( $n ) {

        $l:         length( $text-scale );
        $item:      nth( $text-scale, ( $l - $n ) );
        $key:       nth( $item, 1 );

        @return ".#{ $font-size-prop }#{ $key }";
    }

    h1 { @extend #{ heading(1) }; }
    h2 { @extend #{ heading(2) }; }
    h3 { @extend #{ heading(3) }; }
    h4 { @extend #{ heading(4) }; }
    h5 { @extend #{ heading(5) }; }
    h6 { @extend #{ heading(6) }; }

    h1, h2, h3, h4, h5, h6 {

        font-family: $font-sans;
        font-weight: inherit;

    }

}
